import React from 'react';
import PropTypes from 'prop-types';
import { useNavigate } from 'react-router-dom';

// @mui
import { useTheme } from '@mui/material/styles';
import {
  Card,
  Stack,
  Typography,
  CardContent,
  Button,
  Chip
} from '@mui/material';

// ----------------------------------------------------------------------

const PricingCard = (props) => {
  const { badge, price, specifications, action } = props;
  const theme = useTheme();
  const navigate = useNavigate();

  // const handleDetail = (id) => {
  //     navigate(`/pages/blogs/${id}`)
  // }

  return (
    <Card sx={{ width: '100%', maxWidth: 300 }}>
      <CardContent>
        <Stack spacing={3} justifyContent={'center'} alignItems={'center'}>
          <Chip label={badge.label} color={badge.color || 'primary'} />
          <Typography variant="h1" sx={{ color: theme.palette.text.default }}>
            {price.currency}
            {price.value}
            <Typography variant="h4" component="span">
              /{price.type}
            </Typography>
          </Typography>
          <Stack spacing={1}>
            {specifications?.map((spec, index) => (
              <Stack direction="row" spacing={2}>
                {spec.includes ? (
                  <Typography
                    key={index}
                    variant="h5"
                    fontWeight={200}
                    sx={{
                      fontWeight: 200,
                      color: 'grey.500'
                    }}
                  >
                    √
                  </Typography>
                ) : (
                  <Typography
                    component="span"
                    variant="h5"
                    sx={{
                      fontWeight: 200,
                      color: 'grey.500'
                    }}
                  >
                    —
                  </Typography>
                )}
                <Typography
                  key={index + spec.lable}
                  variant="h5"
                  sx={{
                    fontWeight: 200,
                    color: 'grey.500'
                  }}
                >
                  {spec.label}
                </Typography>
              </Stack>
            ))}
          </Stack>
          <Button variant="contained" onClick={() => navigate(action.route)}>
            {action.label}
          </Button>
        </Stack>
      </CardContent>
    </Card>
  );
};

PricingCard.propTypes = {
  post: PropTypes.object.isRequired,
  index: PropTypes.number
};

export default PricingCard;
